import React from 'react';
import styled from 'styled-components';

import {Container, IconedList, SectionHeader, AdaptiveBr, SkewSection} from 'ui';
import fraudIcon from 'assets/images/unique-features/fraud-prevention.svg';
import promoIcon from 'assets/images/unique-features/promo-abuse.svg';
import accountIcon from 'assets/images/unique-features/account-takeover&abuse.svg';
import reputationIcon from 'assets/images/unique-features/reputation-score.svg';
import humanIcon from 'assets/images/human.svg';
import earthIcon from 'assets/images/earth.svg';

const EarthContainer = styled.div`
  transform: translateY(105px);
  position: relative;
  width: 100%;
  
  
  img {
    width: 100%; 
    transform: rotate(-0.2deg);
  }
  
  @media (max-width: 600px) {
    display: none;
  }
`;

const List = IconedList.extend`
 li:first-child {
  margin-top: 20px !important;
 }
`;

const CountryContainer = styled.div`
  display: flex;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  padding-bottom: 5%;
  justify-content: space-between;
  align-items: flex-start;
`;

const Country = styled.div`
  display: flex;
  align-items: center;
  position: relative;
  top: ${p => p.top};
  box-shadow: 0 4px 9px 0 rgba(22, 33, 83, 0.14);
  border-radius: 4px;
  padding: 14px 24px;
  background-color: #fff;
  color: #3E466D;
  ${p => p.alignSelf && `align-self: ${p.alignSelf}`};
  
  @media (max-width: 900px) {
    padding: 6px 12px; 
  }
  
  @media (max-width: 760px) {
    &:nth-child(2) {
      display: none;
    }
  }
`;

const CountryIcon = styled.div``;

const CountryDetails = styled.div`
  font-size: 12px;
  font-weight: 600;
`;

const CountryField = styled.div`
  margin: 0 10px;
  line-height: 18px;
`;

const Section = SkewSection.extend`
    padding: 50px 0;
    margin: 60px 0 130px;
    
    &:before {
      clip-path: polygon(0 120px, 100% 0, 100% calc(100% - 158px), 0 100%);
    }
  
    &:after {
      clip-path: polygon(0 0, 100% 120px, 100% 100%, 0 calc(100% - 177px));
    }
    
    @media(max-width: 600px) {
      padding-top: 0;
      margin-top: 0;
      padding-bottom: 8px;
      
      &:before {
        clip-path: polygon(0 65px, 100% 0, 100% calc(100% - 65px), 0 100%);
      }
  
      &:after {
        clip-path: polygon(0 0, 100% 65px, 100% 100%, 0 calc(100% - 65px));
      }
    }
`;

export const UniqueFeatures = (props) => (
    <Section {...props}>
        <Container align='center' dir='column'>
            <SectionHeader textAlign='center' margin='60px 0 20px'>
                Unique features <AdaptiveBr maxWidth='530px'/> for businesses
            </SectionHeader>
            <List>
                {features.map((item, key) => (
                    <IconedList.Item descColor='#3E466D' key={key} img={item.icon} title={item.title}>
                        {item.text}
                    </IconedList.Item>
                ))}
            </List>
            <EarthContainer>
                <img src={earthIcon} alt=""/>
                <CountryContainer>
                    {countries.map((item, index) => (
                        <Country top={item.top} key={index} alignSelf={item.alignSelf}>
                            <CountryIcon><img src={item.icon} alt=""/></CountryIcon>
                            <CountryDetails>
                                <CountryField>Score: {item.name}</CountryField>
                                <CountryField>{item.lang}</CountryField>
                                <CountryField>{item.code}</CountryField>
                            </CountryDetails>
                        </Country>
                    ))}
                </CountryContainer>
            </EarthContainer>
        </Container>
    </Section>
);

const countries = [
    {icon: humanIcon, name: '560', lang: 'IP Ratelimited', code: 'Colombo, Sri Lanka', alignSelf: 'flex-end', top: '-10%'},
    {icon: humanIcon, name: '515', lang: 'Accessed from VPN', code: 'Tokyo, Japan', alignSelf: 'flex-start', top: '-7%'},
    {icon: humanIcon, name: '502', lang: 'Promo abuse reported', code: 'Shenzhen, China', alignSelf: 'flex-start'},
    {icon: humanIcon, name: '460', lang: 'Failed to login 23 times', code: 'Orlando, United States', alignSelf: 'flex-end'},
];

const features = [
    {icon: fraudIcon, title: 'Fraud prevention',
     text: `
        One of the core pains that Hive is solving for businesses is the Customer Due Diligence process
        which introduces an internal scoring system along with real-time machine learning to rate the
        accounts on the Network. Hive is a single intelligent solution for different types of fraud.
    `},
    {icon: promoIcon, title: 'Promo abuse',
        text: `
        Hive ecosystem nurtures a more effective promo-code and coupon distribution. With Hive,
        businesses may launch one-time special offers without the risk of depleting their marketing
        budget on the users who are gaming the system by creating the fake accounts.
    `},
    {icon: accountIcon, title: 'Account takeover & abuse',
        text: `
        Account takeover is a growing problem that is most likely going to remain and scale
        significantly. Personal information, monetary value, loyalty points, community integrity,
        and more are at risk during an account takeover attack. Make sure only good users have access
        to their accounts.
    `},
    {icon: reputationIcon, title: 'Reputation score',
        text: `
        Hive enables individuals to establish, maintain, enhance and communicate their reputations
        in a radically new and innovative ways. Much like the individual’s identity is building
        up overtime, so does their reputation. Hive Reputation Score is a powerful measure
        of user’s reliability.
    `},
];


