import React from 'react';
import styled from 'styled-components';

const LogoLink = styled.a`
  display: block;
  padding: ${p => p.padding};
  width: ${p => p.width};
  cursor: pointer;
  width: ${p => p.width};
  transition: all .3s;

  &:hover {
    opacity: .7;
  }

  img {
    width: 100%;
  }
  
  @media (max-width: 500px) {
    &:hover {
      opacity: 1;
    }
  }
`;

export const Logo = ({href, img, alt, ...props}) => (
    <LogoLink href={href} {...props}>
        <img src={img} alt={alt}/>
    </LogoLink>
);


