import React, { Component } from 'react';
import styled from 'styled-components';
import {Link} from 'react-scroll';

import {Navigation} from 'ui';

const css = String.raw;

const LangList = styled.ul`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-size: 16px;
  color: #525558;
  list-style: none;
  padding: 0 21px;
  margin-top: 20px; 
  background-color: rgba(62, 70, 109, .02);
  line-height: 35px;
  
  li {
    width: 50%;
    padding: 11px 0;
  }
`;

const MobileNavList = Navigation.extend`
  height: auto !important;
  position: absolute;
  left: 16px;
  right: 16px;
  top: 16px;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  border-radius: 6px;
  background-color: #fff;
  color: #6743DF;
  font-size: 20px;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.26), 0 15px 26px 0 rgba(47,21,136,0.06), 0 0 11px 0 rgba(47,21,136,0.07);
  display: none;
  will-change: opacity;
  
  li {
    height: auto;
  }
  
  a {
    padding: 14px 0;
    font-weight: 600;
  }
`;

const NavContainer = styled.nav`
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 6px;
`;

const BurgerMenu = styled.button`
  position: relative;
  width: 30px;
  height: 20px;
  z-index: 1000;
  padding: 0;
  border: none;
  background: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  right: 0;
  top: 0;
  ${p => p.open && css`
    right: 20px;
    top: 10px;
    width: 24px;
  `}

  span {
    position: absolute;
    display: block;
    height: 3px;
    width: 100%;
    background-color: ${p => p.open ? '#6743DF' : p.color};
    border-radius: 1.5px;
    //transition: all .3s;
    
    &:nth-child(1) {
      top: 0;
    }
    
    &:nth-child(2) {
      opacity: 1;
      top: 50%;
      transform: translateY(-50%);
    }
    
    &:nth-child(3) {
      bottom: 0;
    }
    
    ${p => p.open && css`
      &:nth-child(1) {
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
      }
    
      &:nth-child(2) {
        display: none;
      }
      
      &:nth-child(3) {
        transform: translateY(50%) rotate(-45deg);
        bottom: 50%;
      }
    `}
  }
`;

const Overlay = styled.div`
  display: ${p => p.open ? 'block' : 'none'};
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
`;

export class MobileNavigation extends Component {
  state = {
    isOpen: false
  };

  onBurgerClick = () => {
    this.setState({isOpen: !this.state.isOpen});
    this.props.toggleHeader(!this.state.isOpen);
    const duration = 200;

    if (this.state.isOpen) {
      animate({
        duration: duration,
        draw: this.navigationHide,
        callback: () => this.navref.style.display = 'none'
      })
    } else {
      this.navref.style.display = 'flex';

      animate({
        duration: duration,
        draw: this.navigationShow
      })
    }
  };

  navigationShow = (progress) => {
    this.navref.style.opacity = progress;
  };

  navigationHide = (progress) => {
    this.navref.style.opacity = 1 - progress;
  };

  render() {
    return (
     <NavContainer>
       <Burger
          open={this.state.isOpen} onClick={this.onBurgerClick}
          color={this.props.isWhite ? '#6743DF' : 'white'}
       />
       <Overlay onClick={this.onBurgerClick} open={this.state.isOpen}/>
       <MobileNavList innerRef={el => this.navref = el} open={this.state.isOpen}>
         <Navigation.Item >
           <Link onClick={this.onBurgerClick} to="top" smooth={true} spy={true} offset={-20}>Hive</Link>
         </Navigation.Item>
         <Navigation.Item>
           <Link onClick={this.onBurgerClick} to="problems" smooth={true} spy={true} offset={-90}>Problem</Link>
         </Navigation.Item>
         <Navigation.Item>
           <Link onClick={this.onBurgerClick} to="solution" smooth={true} spy={true} offset={-120}>Solution</Link>
         </Navigation.Item>
         <Navigation.Item>
           <Link onClick={this.onBurgerClick} to="features" smooth={true} spy={true} offset={-50}>Features</Link>
         </Navigation.Item>
         <Navigation.Item>
           <Link onClick={this.onBurgerClick} to="technology" smooth={true} spy={true} offset={-60}>Technology</Link>
         </Navigation.Item>
         <Navigation.Item>
           <Link onClick={this.onBurgerClick} to="market" smooth={true} spy={true} offset={-20}>Market</Link>
         </Navigation.Item>
         <Navigation.Item>
           <Link onClick={this.onBurgerClick} to="token-sale" smooth={true} spy={true} offset={-70}>Token Sale</Link>
         </Navigation.Item>
         <Navigation.Item>
           <Link onClick={this.onBurgerClick} to="leadership" smooth={true} spy={true} offset={-70}>Leadership</Link>
         </Navigation.Item>
         <Navigation.Item width='100%'>
           <LangList>
             <li>English</li>
             <li>中文 (快来了)</li>
             <li>한국어 (곧 온다.)</li>
             <li>Tiếng Việt (đến sớm)</li>
           </LangList>
         </Navigation.Item>
       </MobileNavList>
     </NavContainer>
    );
  }
}

export function animate(options) {

  const start = performance.now();

  return requestAnimationFrame(function animate(time) {
    let timeFraction = (time - start) / options.duration;
    if (timeFraction > 1) timeFraction = 1;

    const progress = options.timing ? options.timing(timeFraction) : timeFraction;

    options.draw(progress);

    if (timeFraction < 1) {
      requestAnimationFrame(animate);
    } else {
      options.callback && options.callback();
    }
  });
}

const Burger = (props) => (
 <BurgerMenu {...props}>
   <span/>
   <span/>
   <span/>
 </BurgerMenu>
);


