import React, {Component} from 'react';
import styled from 'styled-components';

import requestDone from 'assets/images/request_done.svg';
import * as Sections from 'features/sections';

const PreloadImg = styled.div`
  height: 0;
  width: 0;
  visibility: hidden;
  overflow: hidden;
`;

class App extends Component {
  render() {
    return (
     <main className="app">
       <Sections.Header/>
       <Sections.Hero/>
       <Sections.StrategicPartners/>
       <Sections.Problems name='problems'/>
       <Sections.MicrosoftQuote/>
       <Sections.HiveSolution name='solution'/>
       <Sections.KeyAdvantages/>
       <Sections.UniqueFeatures name='features'/>
       <Sections.HiveVault/>
       <Sections.Technology name='technology'/>
       <Sections.Market name='market'/>
       <Sections.TokenGeneration name='token-sale'/>
       <Sections.Roadmap/>
       <Sections.Leadership name='leadership'/>
       <Sections.EventsAndMedia/>
       <Sections.Footer/>
       <PreloadImg>
         <img src={requestDone} alt=""/>
       </PreloadImg>
     </main>
    );
  }
}

export default App;



