import React, {Component} from 'react';
import styled, {keyframes, ThemeProvider} from 'styled-components';

import check from 'assets/images/check to checkbox.svg';
import whiteCheck from 'assets/images/white-check.svg';
import mistake from 'assets/images/mistake.svg';

const css = String.raw;

const Column = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 44%;
  
  @media (max-width: 960px) {
    width: 100%;
  }
`;

const TextField = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 316px;
  width: ${p => p.width ? p.width : '100%'};
  margin: 20px 0;
  position: relative;
  
  ${p => p.error && css`
    input {
      color: #F37067 !important;
      border: ${p.theme.textInput.error.border} !important;
      background: white !important;
      
      &::placeholder {
        color: #F37067 !important;
      }
    }

    &:after, &:before {
      content: '';
      display: block;
      position: absolute;
      color: #F37067
    }
    
    &:before {
      content: '${p.error}';
      bottom: -16px;
      font-size: 12px;
      font-weight: 600;
    }
    
    &:after {
      bottom: 12px;
      right: 16px;
      height: 24px;
      width: 24px;
      background-size: cover;
      background: url(${mistake}) no-repeat;
    }
  `}
  
  input {
    margin-top: 8px;
    padding: 14px 16px;
    border: 1px solid rgba(255, 255, 255, 0.44);
    border-radius: 4px;
    outline: none;
    background-color: rgba(255, 255, 255, 0.11);
    box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
    box-sizing: border-box;
    color: white;
    font-size: 1rem;
    width: 100%;
    -webkit-appearance: none;
    
    
    &::placeholder {
      color: white;
      opacity: .5;
      font-size: 16px;
    }
    
    // styles for desktop safari (centered placeholder text)
    @media not all and (min-resolution:.001dpcm) {
      @supports (-webkit-appearance:none) {
        &::placeholder {
          line-height: 24px;
        }
      }
    }
    
    &:focus {
      color: rgb(103, 67, 223);
      background-color: #fff;
    }
  }
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  padding: 35px;
  width: 100%;
  
  @media (max-width: 600px) {
    padding: 35px 0;
  }
`;

const Button = styled.button`
  display: flex;
  align-items: center;
  border-radius: 29px;
  padding: 0 33px;
  margin: 32px 0;
  height: 54px;
  border: 1px solid white;
  background-color: #24DE38;
  color: white;
  font-size: 22px;
  font-weight: 800;
  cursor: pointer;
  transition: all .20s ease;
  animation-fill-mode: forwards;
  outline: none;
   
  &:hover {
    transform: translateY(-2px);
  }
  
  @media (max-width: 500px) {
    margin: 20px 0 32px;
  }
`;

