const RadioButton = styled.div`
  padding-left: 10px;
  height: 48px;

  label {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px 16px;
    border: 1px solid rgba(255, 255, 255, 0.44);
    border-radius: 4px;
    outline: none;
    background-color: rgba(255, 255, 255, 0.11);
    box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
    color: white;
    transition: all .2s;
    
    &:hover {
      border: 1px solid rgba(255,255,255,0.81);
    }
  };
  
 
  // styles for desktop safari (size like text input)
  @media not all and (min-resolution:.001dpcm) {
    @supports (-webkit-appearance:none) {
      label {
        height: 52px;
      }    
    }
  }
  
  // styles for iphone safari (size like text input)
  @supports (-webkit-overflow-scrolling: touch) {
    label {
        height: 53px;
      } 
  }
  
  input {
    position: absolute;
    opacity: 0;
  
    &:checked + label {
      background-color: #fff;
      color: rgba(103, 67, 223);
    }
  };
  
  ${p => p.error && css`
    & > label {
      color: #F37067 !important;
      border-color: #F37067 !important;
    }
  `};
`;

const Checkbox = styled.div`
  position: relative;

  label {
    display: block;
    height: 18px;
    width: 18px;
    background-color: #fff;
    border-radius: 2px;
  }
  
  input {
    position: absolute;
    opacity: 0;
    
    &:checked + label {
      background: #fff url("${check}") no-repeat center;
    }
  }
`;

const Label = styled.label`
  font-size: .875rem;
  color: inherit;
  line-height: 1.375rem;
  margin-left: 10px;
  font-weight: 400 !important;
`;

const InputField = styled.div`
  display: flex;
  max-width: 316px;
  margin: ${p => p.margin};
  height: ${p => p.height};
`;

const RadioContainer = styled.div`
  margin: 20px 0;
  
  & > label {
    display: block;
    padding-left: 9px;
    margin-bottom: 8px;
  }
`;

const SpinKeyframe = keyframes`
  from {
    transform: rotate(0);
  }
  
  to {
    transform: rotate(360deg);
  }
`;

const Spiner = styled.span`
  display: block;
  border: 2px solid transparent;
  border-radius: 50%;
  height: 1rem;
  width: 1rem;
  animation: ${SpinKeyframe} 1s infinite ease-in-out;
`;

const formTheme = {
  purple: {
    color: '#fff',
    textInput: {
      boxShadow: '0 4px 7px rgba(103, 67, 223, 0.44)',
      border: '1px solid rgba(255, 255, 255, 0.44)',
      color: 'white',
      placeholder: {
        color: 'white'
      },
      focus: {
        border: '1px solid rgba(255, 255, 255, 0.44)',
        color: '#6743DF'
      },
      hover: {
        border: 'rgba(255, 255, 255, 1)'
      },
      error: {
        border: '1px solid rgba(255, 255, 255, 0.44)'
      }
    },
    button: {
      background: '24DE38',
      color: '#white'
    },
    radiobutton: {
      hover: {
        border: 'white'
      },
      checked: {
        border: 'white',
        color: '#6743DF',
        backgroundColor: 'white'
      }
    },
    checkbox: {
      border: 'none',
      checked: {
        background: 'white',
        icon: check
      },
      hover: {
        border: 'white'
      }
    },
    spinner: {
      borderColor: '#504A8E'
    }
  },
  white: {
    color: '#504A8E',
    textInput: {
      boxShadow: 'none',
      border: '1px solid #DEE0E7',
      color: '#767FAB',
      placeholder: {
        color: '#767FAB'
      },
      focus: {
        border: 'rgba(103, 67, 223, 0.44);',
        color: '#6743DF'
      },
      hover: {
        border: 'rgba(103, 67, 223, 0.44)'
      },
      error: {
        border: '1px solid #F37067'
      }
    },
    button: {
      background: '24DE38',
      color: '#white'
    },
    radiobutton: {
      hover: {
        border: 'rgba(103, 67, 223, 0.44)'
      },
      checked: {
        color: 'white',
        backgroundColor: '#6743DF'
      }
    },
    checkbox: {
      border: '1px solid #DEE0E7',
      checked: {
        background: '#7956EF',
        icon: whiteCheck
      },
      hover: {
        border: 'rgba(103, 67, 223, 1)'
      }
    },
    spinner: {
      borderColor: 'white'
    }
  }
};

const FormContainer = styled.form`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: ${p => p.width ? p.width : '80%'};
  margin-top: 70px;
  color: ${p => p.theme.color};
  margin: ${p => p.margin};
  
  label {
    font-size: 14px;
    font-weight: 600;
  }
  
  @media (max-width: 460px) {
    width: 100%;
  }
  
  ${TextField} {
    input {
      border: ${p => p.theme.textInput.border};
      box-shadow: ${p => p.theme.textInput.boxShadow};
      color: ${p => p.theme.textInput.color};
      transition: all .2s;
      
       &::placeholder {
        color: ${p => p.theme.textInput.placeholder.color};
       }
       
       &:hover {
        border-color: ${p => p.theme.textInput.hover.border};
       }
       
       &:focus  {
        border-color: ${p => p.theme.textInput.focus.border};
        color: ${p => p.theme.textInput.focus.color};
       }
    }
  }
  
  ${RadioButton} {
    label {
      border: ${p => p.theme.textInput.border};
      box-shadow: ${p => p.theme.textInput.boxShadow};
      color: ${p => p.theme.textInput.color};
      font-weight: 400 !important;
      
      &:hover {
        border-color: ${p => p.theme.radiobutton.hover.border} !important;
      }
    }
      
    input {
      &:checked + label {
        background-color: ${p => p.theme.radiobutton.checked.backgroundColor};
        color: ${p => p.theme.radiobutton.checked.color};
        border-color: ${p => p.theme.radiobutton.hover.border};
      }
    }
  }
  
  ${Button} {
    color: ${p => p.theme.button.color};
    background: ${p => p.theme.button.background};
    
    ${Spiner} {
      border-top-color: ${p => p.theme.spinner.borderColor};
    }
  }
  
  ${Checkbox} {
    label {
      border: ${p => p.theme.checkbox.border};${p => p.theme.checkbox.checked.background};
    }
    
    input {
      &:checked + label {
        border-color: ${p => p.theme.checkbox.checked.background};
        background: ${p => p.theme.checkbox.checked.background} url("${p => p.theme.checkbox.checked.icon}") no-repeat center;
      }
    }
    
    &:hover {
      label {
        border-color: ${p => p.theme.checkbox.hover.border};
      }
    }
   }
    
    
  ${RadioContainer} {
    & > label {
      color: ${p => p.theme.color};
    }
  }
`;

export class Form extends Component {
  constructor(props) {
    super();

    this.state = {
      theme: props.white ? formTheme['white'] : formTheme['purple'],
      fields: {
        first_name: {
          value: '',
          id: Math.random(),
          error_msg: '',
          validator: (value) => (value.length > 0 ? "" : 'Please fill this input')
        },
        last_name: {
          value: '',
          id: Math.random(),
          error_msg: '',
          validator: (value) => (value.length > 0 ? "" : 'Please fill this input')
        },
        who_you: {
          value: '',
          id: Math.random(),
          error_msg: '',
          validator: (value) => (value.length > 0 ? "" : 'Please fill this input')
        },
        email: {
          value: '',
          id: Math.random(),
          error_msg: '',
          validator: (value) => {
            const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return re.test(value) ? '' : 'Incorrectly entered e-mail';
          },
        },
        amount: {
          value: '',
          id: Math.random(),
          error_msg: '',
          validator: (value) => (value.length > 0 ? "" : 'Please fill this input')
        },
        test: {
          value: '',
          error_msg: '',
          validator: (value) => (value.length > 0 ? "" : 'Please select on of these options'),
          btc_id: Math.random(),
          eth_id: Math.random(),
        },
        i_want: {
          value: false,
          error_msg: '',
          validator: (value) => false,
          id: Math.random(),
        },
      },
      success: false,
      pending: false,
      error: false
    };
  }

  handleInput = ({target}) => {
    const field = this.state.fields[target.name];
    const value = target.type === 'checkbox' ? target.checked : target.value;

    this.setState({fields: {
      ...this.state.fields,
      [target.name]: {
        ...field,
        value,
        error_msg: ''
      }
    }});
  };

  onSubmit = (event) => {
    event.preventDefault();
    let isError = false;

    for (let key in this.state.fields) {
      const field = this.state.fields[key];
      const errorMsg = field.validator(field.value);

      if (errorMsg && !isError) {
        event.target[key].focus && event.target[key].focus();

        this.setState({
          fields: {
            ...this.state.fields,
            [key]: {
              ...field,
              error_msg: errorMsg
            }
          }
        });

        isError = true;
      }
    }

    if (!isError) {

      const body = {
        first_name: this.state.fields.first_name.value,
        last_name: this.state.fields.last_name.value,
        referral: this.state.fields.who_you.value,
        email_address: this.state.fields.email.value,
        amount: this.state.fields.amount.value,
        symbol: this.state.fields.test.value,
        participation_flag: this.state.fields.i_want.value
      };

      console.log(body);

      this.props.onSuccess();

      /*const xhr = new XMLHttpRequest();
      xhr.open('POST', 'https://api-tge.hive.id/whitelist');
      xhr.setRequestHeader('Content-type', 'application/json');
      xhr.onload = e => {
        console.log('ok');

      };

      xhr.send(JSON.stringify(body));*/
    }
  };

  render() {
    const {isPending, ...props} = this.props;
    const fields = this.state.fields;

    return (
     <ThemeProvider theme={this.state.theme}>
       <FormContainer {...props} onSubmit={this.onSubmit}>
         <Column>
           <TextField error={fields.first_name.error_msg}>
             <label htmlFor={fields.first_name.id}>First Name</label>
             <input type="text" name='first_name' id={fields.first_name.id} onChange={this.handleInput} placeholder='John'/>
           </TextField>
           <TextField error={fields.last_name.error_msg}>
             <label htmlFor={fields.last_name.id}>Last Name</label>
             <input type="text" name='last_name' id={fields.last_name.id} onChange={this.handleInput} placeholder='Doe'/>
           </TextField>
           <TextField error={fields.who_you.error_msg}>
             <label htmlFor={fields.who_you.id}>Who referred you?</label>
             <input type="text" name='who_you' id={fields.who_you.id} onChange={this.handleInput} placeholder='Who referred you?'/>
           </TextField>
         </Column>
         <Column>
           <TextField error={fields.email.error_msg}>
             <label htmlFor={fields.email.id}>Email Address</label>
             <input type="email" name='email' id={fields.email.id} onChange={this.handleInput} placeholder='johdoe@example.com'/>
           </TextField>
           <InputField>
             <TextField width='55%' error={fields.amount.error_msg}>
               <label htmlFor={fields.amount.id}>Amount</label>
               <input type="number" min='0' step='0.001' name='amount' id={fields.amount.id} onChange={this.handleInput} placeholder='1 000?'/>
             </TextField>
             <RadioContainer>
               <label>Is the amount in</label>
               <InputField height='48px'>
                 <RadioButton error={fields.test.error_msg}>
                   <input type="radio" name='test' value='ETH' checked={fields.test.value === 'ETH'} onChange={this.handleInput} id={fields.test.eth_id}/>
                   <label htmlFor={fields.test.eth_id}>ETH</label>
                 </RadioButton>
                 <RadioButton error={fields.test.error_msg}>
                   <input type="radio" name='test' value='BTC' checked={fields.test.value === 'BTC'} onChange={this.handleInput} id={fields.test.btc_id}/>
                   <label htmlFor={fields.test.btc_id}>BTC</label>
                 </RadioButton>
               </InputField>
             </RadioContainer>
           </InputField>
           <InputField margin='45px 0 0 0'>
             <Checkbox>
               <input type="checkbox" name='i_want' onChange={this.handleInput} id={fields.i_want.id}/>
               <label htmlFor={fields.i_want.id}/>
             </Checkbox>
             <Label htmlFor={fields.i_want.id}>I want to participate later, but I want to be aware of all the events</Label>
           </InputField>
         </Column>
         <ButtonContainer>
           <Button disabled={isPending}>
             {isPending ? <Spiner/> : 'Send Information'}
           </Button>
         </ButtonContainer>
       </FormContainer>
     </ThemeProvider>
    );
  }
}


