import React from 'react';
import styled from 'styled-components';

const Navigation = styled.ul`
  display: flex;
  color: inherit;
  list-style: none;
  
  &, li, a {
    height: 100%;
  }
`;

const NavItemWrapper = styled.li`
  color: ${p => p.active ? '#cfbdfb' : 'inherit'};
  width: ${p => p.width ? p.width : 'auto'};
  
  &:last-child {
    a {
      padding-right: 0;
    }
  }
  
  a {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0 25px;
    font-weight: 600;
    
  
    &:hover {
      opacity: .8;
    }
  }
`;

const NavigationItem = ({children, ...props}) => (
    <NavItemWrapper {...props}>
        {children}
    </NavItemWrapper>
);

Navigation.Item = NavigationItem;

export {Navigation};


