import React, {Component} from 'react';
import styled from 'styled-components';
import {animateScroll} from 'react-scroll';
import ReactModal from 'react-modal';

import {Container, SectionHeaderWhite, Logo, Form, Socials, ThankForm} from 'ui';
import pattren from 'assets/images/pattern-footer-small.png';
import logoImage from 'assets/images/logo.svg';

import twitter from 'assets/images/social/twitter.svg';
import facebook from 'assets/images/social/facebook.svg';
import linkedin from 'assets/images/social/linkedin.svg';
import telegram from 'assets/images/social/telegram.svg';
import wechat from 'assets/images/social/wechat.svg';
import angel from 'assets/images/social/angel.co.svg';
import crunchbase from 'assets/images/social/crunchbase.svg';

const Section = styled.footer`
  background-color: #925DFB;
  background-image: url(${pattren}), linear-gradient(45deg, #925DFB 100%, #6743DF 100%);;
  background-repeat: no-repeat;
  background-position: 50% -155px;
  padding-top: 200px;
`;

const SectionHeader = SectionHeaderWhite.extend`
  @media (max-width: 500px) {
    font-size: 1.6rem;
  }
`;

const SectionSubHeader = styled.p`
  color: #fff;
  font-size: 1.125rem;
  margin: 0;
  
  @media (max-width: 500px) {
    font-size: 1rem;
    text-align: center;
  }
`;

const FooterSection = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
  width: 100%;
  border-top: 1px solid rgba(255, 255, 255, 0.09);
`;

const FooterNav = styled.div`
  display: flex;
  color: white;
  font-size: .75rem;
  
  p {
    margin: 0;
  }
  
  & > * {
    opacity: .7;
    padding-left: 20px;
  }
  
  a {
    transition: all .30s ease;
    
    &:hover {
      opacity: 1;
    }
  }
`;

export class Footer extends Component {
  state = {
    showModal: false,
  };

  openModal = () => {
    this.setState({showModal: true});
  };

  closeModal = () => {
    this.setState({showModal: false});
  };

  render() {
    return (
     <Section>
       <Container dir='column' align='center'>
         <SectionHeader textAlign='center' notIcon>Hive Token Pre-sale Form</SectionHeader>
         <SectionSubHeader>Join the revolution in digital identity. Be the owner of your data!</SectionSubHeader>
         <Form onSuccess={() => this.setState({showModal: true})} name={this.props.name}/>
         <Socials socials={socials}/>
         <FooterSection>
           <div>
             <Logo onClick={() => animateScroll.scrollToTop()} img={logoImage} src='/' alt='Hive' width='76px'/>
           </div>
           <FooterNav>
             <p>© 2018 Hive Identity Technologies</p>
             <a href="/terms">Terms</a>
             <a href="/privacy">Privacy</a>
           </FooterNav>
         </FooterSection>
       </Container>
       <ReactModal
        isOpen={this.state.showModal}
        onRequestClose={this.closeModal}
        className="modal-content pre-sale"
        overlayClassName="modal-overlay"
       >
         <ThankForm onClick={this.closeModal}/>
       </ReactModal>
     </Section>
    );
  }
}

const socials = [
  {img: twitter, href: 'https://twitter.com/hiveidentity'},
  {img: facebook, href: 'https://www.facebook.com/hiveidentity'},
  {img: linkedin, href: 'https://www.linkedin.com/company/hiveidentity'},
  {img: telegram, href: 'https://t.me/Hive_TGE'},
  {img: wechat, href: 'https://weixin.qq.com/g/AipcMTm3zVuYbS_8'},
  {img: angel, href: 'https://www.crunchbase.com/organization/hiveidentity'},
  {img: crunchbase, href: 'https://angel.co/hiveidentity'},
];


