import React, {Component} from 'react';
import styled from 'styled-components';
import {colors} from 'ui/theme';

const TimerWrapper = styled.div`
  display: flex;
  font-size: ${p => p.size ? p.size : '2rem'};
  //font-weight: bold;
  margin: 0 -10px;
`;

const TimerItem = styled.div`
  color: #ffb300;
  margin: 0 10px;
  position: relative;
  padding-bottom: 30px;
  //font-weight: 900;
  
  &:before {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    font-size: 1rem;
    //font-weight: bold;
    color: #3e466d;
    opacity: .7;
  }
`;

const TimerDays = TimerItem.extend`
  &:before {
    content: 'D';
  }
`;

const TimerHours = TimerItem.extend`
  &:before {
    content: 'H';
  }
`;

const TimerMinutes = TimerItem.extend`
  &:before {
    content: 'M';
  }
`;

const TimerSeconds = TimerItem.extend`
  &:before {
    content: 'S';
  }
`;

const Delimiter = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 7%;
  margin: 0 5px;

  &:before, &:after {
    content: '';
    display: block;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background-color: #ffb300;
  }
  
  &:after {
    display: ${p => p.one ? 'none' : 'block'};
    margin-top: 5px;
  }
  
  @media (max-width: 650px) {
    padding-bottom: 10%;
    
    &:before, &:after {
      width: 5px;
      height: 5px;
    }
  }
`;

export class Timer extends Component {
    constructor(props) {
        super();

        this.state = {
            seconds: (+props.to - Date.now()) / 1000
        };
    }

    componentDidMount = () => {
        setInterval(() => {
            this.setState({seconds: this.state.seconds + (this.props.inverse ? -1 : 1)});
        }, 1000)
    };

    getSeconds = () => this.state.seconds % 60 | 0;
    getMinutes = () => this.state.seconds / 60 % 60 | 0;
    getHours = () => this.state.seconds / 60 / 60 % 24 | 0;
    getDays = () => this.state.seconds / 60 / 60 / 24 | 0;
    format = time => (time + '').padStart(2, '0');

    render() {
        return (
            <TimerWrapper {...this.props}>
                <TimerDays>{this.format(this.getDays())}</TimerDays>
                <Delimiter one/>
                <TimerHours>{this.format(this.getHours())}</TimerHours>
                <Delimiter/>
                <TimerMinutes>{this.format(this.getMinutes())}</TimerMinutes>
                <Delimiter/>
                <TimerSeconds>{this.format(this.getSeconds())}</TimerSeconds>
            </TimerWrapper>
        );
    }
}


