import React, {Component} from 'react';
import styled from 'styled-components';
import {animateScroll} from 'react-scroll';

import logoImage from 'assets/images/logo-purple.svg';
import logoImageWhite from 'assets/images/logo.svg';
import {Logo, Container} from 'ui';
import {MobileNavigation} from './molecules/mobile-navigation';
import {DesktopNavigation} from './molecules/desktop-navigation';

const css = String.raw;

const HeaderSection = styled.header`
  height: 72px;
  position: fixed;
  width: 100vw;
  z-index: 1000;
  transition: all .4s ease-in-out;
  ${p => p.isWhite ? css`
    background: #fff;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .08);
  ` : css`
    background: transparent;
  `}
  
  
  ${p => p.hideBg && css`
    background: transparent;
  `};
`;

const HeaderContainer = Container.extend`
  position: relative;
`;

export class Header extends Component {
  state = {
    isWhite: false,
    hideBg: false,
    mobile: this.isMobile()
  };

  toggleHeader = (hideBg) => {
      this.setState({hideBg});
  };

  componentDidMount() {
    window.addEventListener('scroll', this.onScroll);
    window.addEventListener('resize', this.onResize);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll);
    window.removeEventListener('resize', this.onResize);
  }

  onScroll = () => {
    const offset = window.pageYOffset;
    if (offset < this.getOffset()) {
      this.setState({isWhite: false});
    } else {
      this.setState({isWhite: true});
    }
  };

  getOffset() {
    return this.isMobile() ? 20 : 100;
  }

  isMobile() {
    return window.innerWidth <= 1000;
  }

  onResize = () => {
    if (this.isMobile()) {
      this.setState({mobile: true});
    } else {
      this.setState({mobile: false});
    }
  };

  render() {
    return (
     <HeaderSection hideBg={this.state.hideBg} onresize={this.onResize} isWhite={this.state.isWhite}>
       <HeaderContainer justify='space-between' align='stretch'>
         <Logo
          onClick={() => animateScroll.scrollToTop()}
          img={this.state.isWhite ? logoImage : logoImageWhite}
          src='#' alt='Hive' width='100px' padding='16px 0'/>
         {this.state.mobile ?
            <MobileNavigation toggleHeader={this.toggleHeader} isWhite={this.state.isWhite}/> :
            <DesktopNavigation isWhite={this.state.isWhite}/>}
       </HeaderContainer>
     </HeaderSection>
    )
  }
}


