import styled from 'styled-components';

export const SkewSection = styled.section`
  position: relative;
  padding: ${p => p.padding};
  margin: ${p => p.margin};

  &:before, &:after {
    content: '';
    position: absolute;
    display: block;
    left: 0;
    top: -6%;
    width: 100%;
    height: 115%;
    z-index: -1;
  }
  
  &:before {
    clip-path: polygon(0 120px, 100% 0, 100% calc(100% - 120px), 0 100%);
    background: ${p => p.secondBg ? p.secondBg : '#F2F2FD'};
  }
  
  &:after {
    clip-path: polygon(0 0, 100% 120px, 100% 100%, 0 calc(100% - 120px));
    background-size: cover;
    background: ${p => p.firstBg ? p.firstBg : '#F9F9FF'};
  }
  
  @media (max-width: 600px) {
    &:before {
      clip-path: polygon(0 65px, 100% 0, 100% calc(100% - 65px), 0 100%);
      background: ${p => p.secondBg ? p.secondBg : '#F2F2FD'};
    }
  
    &:after {
      clip-path: polygon(0 0, 100% 65px, 100% 100%, 0 calc(100% - 65px));
      background-size: cover;
      background: ${p => p.firstBg ? p.firstBg : '#F9F9FF'};
    }
  }
`;

