import React, {Component} from 'react';
import styled from 'styled-components';

import {Container, SectionHeaderWhite, SkewSection, AdaptiveBr, Books, Chart} from 'ui';
import bg from 'assets/images/patterns/pattern-on-purple-bg.png';
import diagramLogo from 'assets/images/logo to diagram.svg';

const css = String.raw;

const Header = SectionHeaderWhite.extend`
  margin: 20px 0 80px;
  
  @media (max-width: 600px) {
    margin-bottom: 40px;
  }
`;

const TokenItem = styled.li`
  padding: 10px 20px;
  max-width: 479px;
  
  h5 {
    margin: 0;
    font-size: 1rem;
    line-height: 1.5rem;
    font-weight: 900;
  }
  
  p {
    margin: 5px 0;
    font-size: 1rem;
    line-height: 1.5rem;
  }
  
  @media (max-width: 500px) {
    h5 {
      margin: 5px 0;
    }
  }
`;

const TokensItems = styled.ul`
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: white;
  list-style: none;
  max-width: 1024px;
  margin-bottom: 48px;
  ${p => p.margin && `margin: ${p.margin}`}
  
  @media (max-width: 760px) {
    flex-direction: column;
    
    ${TokenItem} {
      display: flex;
      
      h5 {
        min-width: 210px;
      }
    }
  }
  
  @media (max-width: 760px) {
    ${TokenItem} {
      h5 {
        min-width: 0;
        width: 35%;
        padding-right: 5px;
      }
      
      p {
        width: 65%;
      }
    }
  }
`;

const BooksContainer = styled.div`
  margin-top: 50px;
  width: 85%;
  margin-bottom: -25%;
  
  @media (max-width: 700px) {
    width: 100%;
  }
`;

const Section = SkewSection.extend`
  padding: 100px 0 100px;
  margin: 130px 0 200px;
  
  @media(max-width: 570px) {
    padding-top: 30px;
    padding-bottom: 10px;
    margin-bottom: 100px;
    
    &:before, &:after {
      top: -3%;
      height: 100%;
    }
  }
`;

const ChartContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const SwitchButtonContainer = styled.div`
  display: flex;
  align-items: center;
  font-size: 18px;
  font-weight: 900;
  color: white;
  
  & > div {
    margin: 0 23px;
  }
  
  @media(max-width: 500px) {
    font-size: 17px;
    
    & > div {
      margin: 0 15px;
      text-align: center;
    }  
  }
`;

const SwitchButton = styled.label`
  height: 30px;
  width: 80px;
  border-radius: 17px;
  background-color: #fff;
  position: relative;
  
  &:before {
    content: '';
    display: block;
    height: 22px;
    width: 22px;
    position: absolute;
    background-color: #6743DF;
    border-radius: 50%;
    margin: 4px 5px;
    transition: all .3s ease-in-out;
    left: 0;
    
    ${p => p.check && css`
      left: 60%;
    `};
  }
  
  @media(max-width: 500px) {
    height: 26px;
    width: 55px;
    
    &:before {
      height: 18px;
      width: 18px;
      ${p => p.check && css`
        left: 48%;
      `};
    }
  }
  
  input {
    display: none;
  }
`;

const ChartWrapper = styled.div`
  display: flex;
  align-items: center;
  margin: 50px 0;
  
  @media (max-width: 760px) {
    flex-direction: column;
  }
`;

const Legend = styled.ul`
  margin-left: 30px;
  height: 225px;
  
  @media (max-width: 760px) {
    margin-top: 30px;
  }
`;

const LegendItem = styled.li`
  display: flex;
  align-items: center;
  color: white;
  font-size: 1rem;
  font-weight: 500;
  margin: 12px 5px;
  width: 270px;
  
  
  span {
    display: block;
    min-width: 36px;
    margin: 0 8px;
    font-weight: bold;
  }
  
  &:before {
    content: '';
    display: block;
    margin-right: 4px;
    width: 11px;
    height: 11px;
    background-color: ${p => p.color};
    border-radius: 50%;
  }
`;

export function prepareData(data) {
  let lastAngle = 0;
  let rad = 0.0174533;
  let grad = 360 / 100;

  return data.map(item => {
    const startAngle = lastAngle;
    const endAngle = lastAngle + (item.percent * grad * rad);

    lastAngle = endAngle;

    return {
      ...item,
      startAngle,
      endAngle,
      to: {
        startAngle: startAngle,
        endAngle: startAngle,
      },
      from: {
        startAngle: startAngle,
        endAngle: endAngle,
      }
    }
  })
}

const d = [
  {label: 'Sales & Marketing', fill: '#2DC1D4', percent: 45, id: 'ee'},
  {label: 'Research & Development', fill: '#5200FF', percent: 15, id: 'aa'},
  {label: 'Reserve', fill: '#35CC65', percent: 10, id: 'cc'},
  {label: 'International Expansion', fill: '#E5DB0C', percent: 10, id: 'ff'},
  {label: 'Acquisitions & Partnerships', fill: '#C135CC', percent: 7.5, id: 'gg'},
  {label: 'Legal & Compliance', fill: '#E9E9E9', percent: 7.5, id: 'dd'},
  {label: 'Admin & Operations', fill: '#FF8A02', percent: 5, id: 'bb'},
];

const d5 = [
  {label: 'Reserve', fill: '#2DC1D4', percent: 20, id: 'ee'},
  {label: 'Sale and Incentives', fill: '#35CC65', percent: 65, id: 'cc'},
  {label: 'Team', fill: '#C135CC', percent: 10, id: 'gg'},
  {label: 'Advisors and Bounty', fill: '#FF8A02', percent: 5, id: 'bb'},
];

export class TokenGeneration extends Component {
  state = {
    switch: false,
    chart: {
      settings: {
        radius: 145,
        width: 300,
        height: 300
      },
      data: [
        prepareData(d),
        prepareData(d5)
      ]
    }
  };

  render() {
    const data = this.state.switch ? this.state.chart.data[1] : this.state.chart.data[0];

    return (
     <Section secondBg='#9879FF' firstBg={`#925DFB url(${bg}) center`} name={this.props.name}>
       <Container dir='column' align='center'>
         <Header textAlign='center'>
           Token Generation <AdaptiveBr maxWidth='500px'/>Event (TGE)
         </Header>
         <TokensItems margin='0'>
           <TokenItem>
             <h5>Name</h5>
             <p>Hive ID (HID)</p>
           </TokenItem>
           <TokenItem>
             <h5>Total Supply</h5>
             <p>700 000 000 (no new tokens will ever be created)</p>
           </TokenItem>
           <TokenItem>
             <h5>Token Value</h5>
             <p>1 HID = 0.10 USD</p>
           </TokenItem>
           <TokenItem>
             <h5>Soft Cap</h5>
             <p>$5 000 000</p>
           </TokenItem>
           <TokenItem>
             <h5>Hard Cap</h5>
             <p>$25 000 000</p>
           </TokenItem>
         </TokensItems>
         <TokensItems>
           <TokenItem>
             <h5>Bonus for Large Purchasers</h5>
             <p>Purchasers who commit to purchase tokens during the Token Sale in the amount
               equal or greater than 500,000 HID, will be eligible for a bonus. All such
               bonuses will be discussed individually with each potential buyer.</p>
           </TokenItem>
           <TokenItem>
             <h5>Major Exchanges Listing</h5>
             <p>Q4 2018</p>
           </TokenItem>
           <TokenItem>
             <h5>Unsold Tokens</h5>
             <p>Unsold Tokens</p>
           </TokenItem>
         </TokensItems>
         <ChartContainer>
           <SwitchButtonContainer>
             <div>Token Distribution</div>
             <SwitchButton check={this.state.switch} htmlFor='switch-button'>
               <input type="text" id='switch-button' onClick={() => this.setState({switch: !this.state.switch})}/>
             </SwitchButton>
             <div>Use of Funds</div>
           </SwitchButtonContainer>
           <ChartWrapper>
             <Chart
              {...this.state.chart.settings}
              img={{
                href: diagramLogo,
                height: 82,
                width: 76
              }}
              data={data}
             />
             <Legend>
               {data.map(item => (
                <LegendItem key={item.id} color={item.fill}><span>{item.percent}%</span> {item.label}</LegendItem>
               ))}
             </Legend>
           </ChartWrapper>
         </ChartContainer>
         <BooksContainer>
           <Books/>
         </BooksContainer>
       </Container>
     </Section>
    )
  }
}


