import React from 'react';
import styled from 'styled-components';
import requestDone from 'assets/images/request_done.svg';

const ThankFormContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 68px 100px 50px;
  
  h5 {
    font-size: 32px;
    color: #6743DF;
    margin-bottom: 16px;
    font-weight: 900;
  }
  
  p {
    color: #3E466D;
    text-align: center;
    line-height: 24px;
    width: 80%;
    margin: 0;
    
    @media (max-width: 500px) {
      width: 100%;
    }
  }
  
  @media (max-width: 600px) {
    padding: 48px;
  }
  
  @media (max-width: 500px) {
    padding: 48px 20px;
  }
`;

const ButtonOk = styled.button`
  color: white;
  box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
  cursor: pointer;
  padding: 12px 64px;
  margin-top: 35px;
  border-radius: 24px;
  font-weight: 900;
  outline: none;
  background: linear-gradient(45deg, #925DFB 0%, #6743DF 100%);
  border: none;
  transition: all .20s ease;
  animation-fill-mode: forwards;
    
  &:hover {
    transform: translateY(-2px);
  }
`;

export const ThankForm = ({onClick}) => (
 <ThankFormContainer>
   <img src={requestDone} alt=""/>
   <h5>Thank You!</h5>
   <p>We perform KYC/AML on potential contributors. Filling out this form does not guarantee
     you will be allowed to contribute. We will reply shortly with a unique link generated
     just for you to start KYC/AML process.</p>
   <ButtonOk onClick={onClick}>Ok</ButtonOk>
 </ThankFormContainer>
);


