import React, {Component} from 'react';
import styled from 'styled-components';
import ReactModal from 'react-modal';

import {Timer, Container, Form, SectionHeader} from 'ui';
import {colors} from 'ui/theme';
import timeImg from 'assets/images/watch.svg';
import requestDone from 'assets/images/request_done.svg';

import check from 'assets/images/check to checkbox.svg';

ReactModal.setAppElement('#root');

const SectionSubHeader = styled.p`
  font-size: 1.125rem;
  margin: 0;
  padding: 0;
  color: #3E466D;
`;

const PrivateSaleWrapper = styled.div`
  padding: 120px 0 150px;
`;

const SaleTitleWrapper = styled.div`
  display: flex;
  align-items: center;
  
  img {
    margin-right: 20px;
  }
`;

const SaleTitle = styled.h3`
  font-size: 2rem;
  font-weight: 900;
  color: ${colors.main};
`;

const SaleDescription = styled.p`
  margin-top: 24px;
  color: #4a527d;
  width: 85%;
`;

const Button = styled.button`
  background: linear-gradient(45deg, #925DFB 0%, #6743DF 100%);
  color: white;
  font-size: 1.5rem;
  border-radius: 30px;
  padding: 15px 90px;
  border: none;
  cursor: pointer;
  outline: none;
  font-weight: 900;
  box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
  margin-top: 3px;
  
  transition: all .20s ease;
  animation-fill-mode: forwards;
    
  &:hover {
    transform: translateY(-2px);
  }
`;

const CardFooter = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 38px;
  
  @media (max-width: 990px) {
   flex-direction: column;
   align-items: center;
   
  
    ${Button} {
      width: 100%;
      margin-top: 50px;
    }
  }
  
  @media (max-width: 650px) {
   font-size: .7rem;
  }
  
  @media (max-width: 370px) {
   font-size: .5rem;
   
   ${Button} {
      margin-left: 0;
      margin-right: 0;
    }
  }
`;

const SaleCard = styled.div`
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: 64px 130px; 
  box-shadow: 0 0 11px rgba(47, 21, 136, 0.07), 0 15px 26px rgba(47, 21, 136, 0.06);
  border-radius: 16px;
  
  @media (max-width: 1150px) {
    padding: 50px 60px;
  }
  
  @media (max-width: 990px) {
    padding: 50px 30px;
  }
  
  @media (max-width: 650px) {
    width: 100%;
  }
`;

/*const ModalCloseButton = styled.button`
  position: absolute;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  border: none;
  cursor: pointer;
  background: url(${closeImg}) no-repeat center;
  outline: none;
`;*/

const ThankFormContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 68px 100px 50px;
  
  h5 {
    font-size: 32px;
    color: #6743DF;
    margin-bottom: 16px;
    font-weight: 900;
  }
  
  p {
    color: #3E466D;
    text-align: center;
    line-height: 24px;
    width: 80%;
    margin: 0;
  }
`;

const ButtonOk = styled.button`
  color: white;
  box-shadow: 0 4px 7px rgba(103, 67, 223, 0.44);
  cursor: pointer;
  padding: 12px 64px;
  margin-top: 35px;
  border-radius: 24px;
  font-weight: 900;
  outline: none;
  background: linear-gradient(45deg, #925DFB 0%, #6743DF 100%);
  
  transition: all .20s ease;
  animation-fill-mode: forwards;
    
  &:hover {
    transform: translateY(-2px);
  }
`;

const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-bottom: 16px;
`;

const ContainerForLoadImages = styled.div`
  position: absolute;
  left: 10000px;
  background: url(${check});
  display: none;
`;

export class PrivateSale extends Component {
  state = {
    showModal: false,
    form: {
      success: false,
      pending: false,
      error: false
    }
  };

  onSubmit = (event) => {
    event.preventDefault();

    this.setState({form: {...this.state.form, pending: true}});

    setTimeout(() => {
      this.setState({form: {success: true, error: false, pending: false}});
    }, 1000)
  };

  openModal = () => {
    this.setState({showModal: true});
  };

  closeModal = () => {
    this.setState({showModal: false});
  };

  render() {
    return (
     <PrivateSaleWrapper>
       <Container>
         <SaleCard>
           <SaleTitleWrapper>
             <img src={timeImg} alt="time"/>
             <SaleTitle>Private Pre-Sale OPEN</SaleTitle>
           </SaleTitleWrapper>
           <SaleDescription>Join the revolution in digital identity. Be the owner of your data!</SaleDescription>
           <CardFooter>
             <Timer size='3.5em' seconds='70000' to='1535749200000' inverse/>
             <Button onClick={this.openModal}>Join Us</Button>
           </CardFooter>
         </SaleCard>
       </Container>
       <ReactModal
        isOpen={this.state.showModal}
        onRequestClose={this.closeModal}
        className="modal-content pre-sale"
        overlayClassName="modal-overlay"
       >
         {this.state.form.success ? (
          <ThankFormContainer>
            <img src={requestDone} alt=""/>
            <h5>Thank You!</h5>
            <p>We perform KYC/AML on potential contributors. Filling out this form does not guarantee
              you will be allowed to contribute. We will reply shortly with a unique link generated
              just for you to start KYC/AML process.</p>
            <ButtonOk onClick={this.closeModal}>Ok</ButtonOk>
          </ThankFormContainer>
         ) : (
          <FormContainer>
            <SectionHeader textAlign='center' notIcon margin='64px 0 16px 0'>Hive Token Pre-sale Form</SectionHeader>
            <SectionSubHeader>Join the revolution in digital identity. Be the owner of your data!</SectionSubHeader>
            <Form margin='36px 0 0' isPending={this.state.form.pending} onSubmit={this.onSubmit} width='100%' white/>
          </FormContainer>
         )}
       </ReactModal>
       <ContainerForLoadImages>
         <img src={requestDone} alt=""/>
       </ContainerForLoadImages>
     </PrivateSaleWrapper>
    );
  }
}




