import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-scroll';

import {Navigation} from 'ui';
import {LanguageSelect} from '../organisms/language-select';

const NavContainer = styled.nav`
  display: block;
  padding-top: 6px;
  color: ${p => p.color};
`;

export const DesktopNavigation = (props) => (
 <NavContainer color={props.isWhite ? '#6743DF' : 'white'}>
   <Navigation>
     <Navigation.Item>
       <Link to="problems" smooth={true} spy={true} offset={-20}>Problem</Link>
     </Navigation.Item>
     <Navigation.Item>
       <Link to="solution" smooth={true} spy={true} offset={-120}>Solution</Link>
     </Navigation.Item>
     <Navigation.Item>
       <Link to="features" smooth={true} spy={true}>Features</Link>
     </Navigation.Item>
     <Navigation.Item>
       <Link to="technology" smooth={true} spy={true} offset={-20}>Technology</Link>
     </Navigation.Item>
     <Navigation.Item>
       <Link to="market" smooth={true} spy={true} offset={-20}>Market</Link>
     </Navigation.Item>
     <Navigation.Item>
       <Link to="token-sale" smooth={true} spy={true}>Token Sale</Link>
     </Navigation.Item>
     <Navigation.Item>
       <Link to="leadership" smooth={true} spy={true} offset={-70}>Leadership</Link>
     </Navigation.Item>
     <Navigation.Item>
       <LanguageSelect color={props.isWhite ? '#6743DF' : 'white'}/>
     </Navigation.Item>
   </Navigation>
 </NavContainer>
);


