import LatoBlackEOT from 'assets/fonts/lato-black.eot';
import LatoBlackWOFF2 from 'assets/fonts/lato-black.woff2';
import LatoBlackWOFF from 'assets/fonts/lato-black.woff';
import LatoBlackSVG from 'assets/fonts/lato-black.svg';

import LatoBoldEOT from 'assets/fonts/lato-bold.eot';
import LatoBoldWOFF2 from 'assets/fonts/lato-bold.woff2';
import LatoBoldWOFF from 'assets/fonts/lato-bold.woff';
import LatoBoldSVG from 'assets/fonts/lato-bold.svg';

import LatoSemiboldEOT from 'assets/fonts/lato-semibold.eot';
import LatoSemiboldWOFF2 from 'assets/fonts/lato-semibold.woff2';
import LatoSemiboldWOFF from 'assets/fonts/lato-semibold.woff';
import LatoSemiboldSVG from 'assets/fonts/lato-semibold.svg';

import LatoRegularEOT from 'assets/fonts/lato-regular.eot';
import LatoRegularWOFF2 from 'assets/fonts/lato-regular.woff2';
import LatoRegularWOFF from 'assets/fonts/lato-regular.woff';
import LatoRegularSVG from 'assets/fonts/lato-regular.svg';

import LatoLightEOT from 'assets/fonts/lato-light.eot';
import LatoLightWOFF2 from 'assets/fonts/lato-light.woff2';
import LatoLightWOFF from 'assets/fonts/lato-light.woff';
import LatoLightSVG from 'assets/fonts/lato-light.svg';

const css = String.raw;

export const font = {
  main: 'Lato, sans-serif'
};

export const colors = {
  main: '#6743DF'
};

export const globalStyles = css`
  @font-face {
    font-family: 'Lato';
    src: url(${LatoBlackEOT});
    src: url(${LatoBlackWOFF2}) format('woff2'),
    url(${LatoBlackWOFF}) format('woff'),
    url(${LatoBlackSVG}) format('svg');
    font-weight: 900;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Lato';
    src: url(${LatoBoldEOT});
    src: url(${LatoBoldWOFF2}) format('woff2'),
    url(${LatoBoldWOFF}) format('woff'),
    url(${LatoBoldSVG}) format('svg');
    font-weight: 700;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Lato';
    src: url(${LatoSemiboldEOT});
    src: url(${LatoSemiboldWOFF2}) format('woff2'),
    url(${LatoSemiboldWOFF}) format('woff'),
    url(${LatoSemiboldSVG}) format('svg');
    font-weight: 600;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Lato';
    src: url(${LatoRegularEOT});
    src: url(${LatoRegularWOFF2}) format('woff2'),
    url(${LatoRegularWOFF}) format('woff'),
    url(${LatoRegularSVG}) format('svg');
    font-weight: 400;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Lato';
    src: url(${LatoLightEOT});
    src: url(${LatoLightWOFF2}) format('woff2'),
    url(${LatoLightWOFF}) format('woff'),
    url(${LatoLightSVG}) format('svg');
    font-weight: 300;
    font-style: normal;
  }

  html, body {
    font-size: 16px;
    font-family: ${font.main};
    -webkit-font-smoothing: antialiased;
    margin: 0;
    padding: 0;
  }
  
  a {
    color: inherit;
    text-decoration: none;
    
    &:hover {
      color: inherit;
      text-decoration: none;
    }
  }
  
  * {
    box-sizing: border-box;
  }
  
  h1, h2, h3, h4 {
    margin: 0;
  }
  
  ul, li {
    margin: 0;
    padding: 0;
  }
  
  
  
  .modal-content {
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 750px;
    margin: 20px 15px;
    background: rgb(255, 255, 255);
    overflow-y: auto;
    border-radius: 4px;
    outline: none;
    padding: 40px;
    box-shadow: 0 1px 40px -11px #2D2342;
    -webkit-overflow-scrolling:touch;
    
    @media (max-width: 760px) {
      flex-direction: column;
      align-items: center;
    }
  }
  
  .pre-sale {
    padding: 0;
    max-width: 850px;
  }
  
  .modal-overlay {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1001;
    background-color: rgba(0, 0, 0, 0.8);
    overflow-y: auto;
	  -webkit-overflow-scrolling:touch;
    
    &-transparent {
      background-color: transparent;
    }
    
    @media (max-width: 960px) {
      align-items: flex-start;
    }
  }
  
  @media (max-width: 500px) {
    * {
      cursor: default !important;
    }
  }
`;


