import React from 'react';
import styled from 'styled-components';

import {Container, SectionHeader} from 'ui';

import jumio from 'assets/images/logotypes/jumio.svg';
import goodhire from 'assets/images/logotypes/goodhire.svg';
import hyperledger from 'assets/images/logotypes/hyperledger.svg';
import linux from 'assets/images/logotypes/linux.svg';
import enecuum from 'assets/images/logotypes/enecuum.svg';
import intercoin from 'assets/images/logotypes/intercoin.png';
import popchest from 'assets/images/logotypes/popchest.svg';

import solution from 'assets/images/partners/solutions.svg';
import crypto from 'assets/images/partners/crypto_law.svg';
import modern from 'assets/images/partners/moderntoken.svg';
import bazaar from 'assets/images/partners/icobazaar.svg';

const Section = styled.section`
  padding: 0 0 100px 0;
`;

const PartnersList = styled.ul`
  display: flex;
  flex-wrap: wrap;
  justify-content: ${p => p.justify ? p.justify : 'space-between'};
  width: 100%;
  list-style: none;
  
  @media (max-width: 450px) {
    flex-direction: column;
    align-items: center;
  }
`;

const PartnersItem = styled.li`
  margin: 20px 35px;
`;

export const Partners = () => (
    <Section>
        <Container dir='column' align='center'>
            <SectionHeader margin='100px 0 40px'>Strategic partners</SectionHeader>
            <PartnersList>
                {partners.slice(0, -3).map(item => <PartnersItem key={item}><img src={item} alt=""/></PartnersItem>)}
            </PartnersList>
            <PartnersList justify='center'>
              {partners.slice(-3).map(item => <PartnersItem key={item}><img src={item} alt=""/></PartnersItem>)}
            </PartnersList>
            <SectionHeader notIcon margin='100px 0 40px'>TGE partners</SectionHeader>
            <PartnersList>
                {tge.map(item => <PartnersItem key={item}><img src={item} alt=""/></PartnersItem>)}
            </PartnersList>
        </Container>
    </Section>
);

const partners = [
    jumio, goodhire, hyperledger, linux, enecuum, intercoin, popchest
];

const tge = [
    solution, crypto, modern, bazaar
];


