import React from 'react';
import styled from 'styled-components';

const SocialsItem = styled.li`
  padding: 10px 10px;
  transition: all .30s ease;
  opacity: .7;
  
  &:first-child {
    padding-left: 0;
  }
  
  &:hover {
    opacity: 1 !important;
  }
  
  a {
    display: block;
  }
`;

const SocialsList = styled.ul`
  display: flex;
  list-style: none;
  padding-bottom: 35px;
  
  ${SocialsItem} {
    opacity: ${p => p.opacity};
  }
`;

export const Socials = ({socials, ...props}) => (
 <SocialsList {...props}>
   {socials.map(item => (
    <SocialsItem key={item.img}>
      <a href={item.href} target='_blank'><img src={item.img} alt=""/></a>
    </SocialsItem>
   ))}
 </SocialsList>
);


