import React, {Component} from 'react';
import styled from 'styled-components';
import ReactVisibilitySensor from 'react-visibility-sensor';

import {SectionHeader, Container} from 'ui';
import roadmapIcon from 'assets/images/honeycom-roadmap.svg';

const Section = styled.section`
  padding: 120px 0 0;
  
  @media (max-width: 600px) {
    padding-top: 35%;
  }
`;

const RoadmapCard = styled.div`
  width: 410px;
  padding: 12px 14px;
  box-shadow: 0 15px 26px rgba(47, 21, 136, 0.06), 0 0 11px rgba(47, 21, 136, 0.07);
  transition: all 1s;
  line-height: 1.5rem;
  opacity: 0;
`;

const RoadmapListItem = styled.li`
  display: flex;
  position: relative;
  
  &:after, &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  
  &:before {
    height: 100%;
    width: 2px;
    background-color: #6743DF;
  }
  
  &:after {
    height: 15px;
    width: 15px;
    background: #fff url(${roadmapIcon}) no-repeat;
    background-size: cover;
  }
  
  &:nth-child(odd) {
    text-align: right;
    
    ${RoadmapCard} {
      transform: translateX(100px);
      ${p => p.isVisible && `transform: none;`};
      ${p => p.isVisible && `opacity: 1;`};
    }
  }
  
  &:nth-child(even) {
    justify-content: flex-end;
    
    ${RoadmapCard} {
      transform: translateX(-100px);
      ${p => p.isVisible && `transform: none;`};
      ${p => p.isVisible && `opacity: 1;`};
    }
  }
`;

const RoadmapList = styled.ul`
    display: flex;
    flex-direction: column;
    width: 80%;
    margin-top: 65px;
    list-style: none;
    
    h5 {
      margin-top: 0;
      margin-bottom: 10px;
      color: #6743DF;
      font-size: 1.125rem;
      font-weight: 700;
    }
    
    p {
      margin: 3px 0;
      color: #3E466D;
      font-size: .875rem;
    }
    
    @media (max-width: 1100px) {
      ${RoadmapCard} {
        width: 300px;
      }
    }
    
    @media (max-width: 850px) {
      width: 100%;
    }
    
    @media (max-width: 680px) {
      ${RoadmapCard} {
        width: 250px;
      }
    }
    
    @media (max-width: 570px) {
      margin-top: 30px;
      
      ${RoadmapListItem} {
        padding: 10px 30px;
        text-align: left;
      }
      
      ${RoadmapListItem} {
        &:after, &:before {
          left: 0;
          top: 23px;
        }
      }
      
      ${RoadmapCard} {
        width: 100%;
        transform: none !important;
      }
    }
`;

export const Roadmap = () => (
    <Section>
        <Container dir='column' align='center'>
            <SectionHeader>Roadmap</SectionHeader>
            <RoadmapList>
                {roadmapData.map(item => (
                    <RoadmapItem key={item.title} title={item.title} process={item.process}/>
                ))}
            </RoadmapList>
        </Container>
    </Section>
);

class RoadmapItem extends Component {
    state = {
        isVisible: false
    };

    onChange = (isVisible) => {
        isVisible && this.setState({isVisible});
    };

    render() {
        return (
            <ReactVisibilitySensor onChange={this.onChange}>
                <RoadmapListItem isVisible={this.state.isVisible}>
                    <RoadmapCard>
                        <h5>{this.props.title}</h5>
                      {this.props.process.map((item, index) => <p key={index}><b>[{item.type}]</b> {item.text}</p>)}
                    </RoadmapCard>
                </RoadmapListItem>
            </ReactVisibilitySensor>
        )
    }
}

const roadmapData = [
    {title: 'Q3 2017', process: [
      {type: 'Process', text: 'Idea inception and market analysis'}
     ]
    },
    {title: 'Q4 2017', process: [
      {type: 'Process', text: 'Blockchain scalability problem research'},
      {type: 'Process', text: 'Development of the platform core started'},
    ]},
    {title: 'Q1 2018', process: [
      {type: 'Process', text: 'Prototype release'},
      {type: 'Process', text: 'Incorporation in Delaware, USA'},
      {type: 'Process', text: 'Seed round: $350 000 raised'},
    ]},
    {title: 'Q2 2018', process: [
      {type: 'Process', text: 'Alpha release'},
      {type: 'Process', text: 'Blockchain technology finalization and integration'},
    ]},
    {title: 'Q3 2018', process: [
      {type: 'Process', text: 'Beta release'},
      {type: 'Process', text: 'Public app release (iOS, Android)'},
      {type: 'Process', text: 'Patent filing in other countries'},
      {type: 'Industry', text: 'Auth plugins for popular platforms and frameworks (Shopify, Wordpress, Passport.js)'},
    ]},
    {title: 'Q4 2018', process: [
      {type: 'Process', text: 'Multi-language support'},
      {type: 'Process', text: 'Employment verification and screening'},
      {type: 'Process', text: 'Education & professional qualifications verification'},
      {type: 'Industry', text: 'Recruiting software integration (Lever, Greenhouse, TriNet, ZipRecruiter)'},
    ]},
    {title: 'Q1 2019', process: [
      {type: 'Geo', text: 'Expansion to Canada'},
      {type: 'Process', text: 'Developer community establishment'},
      {type: 'Industry', text: 'Vending machines & specialty ATMs'},
    ]},
    {title: 'Q2 2019', process: [
      {type: 'Geo', text: 'Expansion to Asia'},
      {type: 'Process', text: '3rd party developer SDK release'},
      {type: 'Industry', text: '3rd party auth solutions integration (Auth0, Okta, Duo Security)'},
    ]},
    {title: 'Q3 2019', process: [
      {type: 'Geo', text: 'Expansion to Europe'},
      {type: 'Industry', text: 'Corporate market solution (MFA, SSO, AD)'},
      {type: 'Industry', text: 'Dedicated FinTech solutions'},
    ]},
    {title: 'Q4 2019', process: [
      {type: 'Process', text: '3rd party app marketplace launch'},
      {type: 'Industry', text: 'Machine-to-machine auth integration (IoT devices)'},
    ]},
];


