import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  position: relative;
  height: 30px;
  width: 100%;
  margin: ${p => p.margin};
  background-color: rgba(255, 179, 0, .2);
  border-radius: 15px;
`;

const Line = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  width: 60%;
  padding-right: 12px;
  border-bottom-left-radius: 15px;
  border-top-left-radius: 15px;
  background-color: #ffb300;
  box-shadow: 0 4px 7px 0 rgba(255, 179, 0, .44);
  color: white;
  font-weight: bold;
  font-size: 12px;
  
  @media (max-width: 550px) {
    font-size: 12px;
  }
`;

export const ProgressBar = ({min, max, current, children, ...props}) => (
    <Wrapper {...props}>
      <Line>{children}</Line>
    </Wrapper>
);


