import {colors} from "ui/theme";
import styled from "styled-components";

import titleImagePurple from 'assets/images/heading.svg';
import titleImageWhite from 'assets/images/heading-white.svg';

export const SectionHeader = styled.h3`
    color: ${p => p.color ? p.color : '#3E466D'};
    font-size: 2rem;
    position: relative;
    margin-bottom: 20px;
    text-align: ${p => p.textAlign};
    font-weight: 700;
    ${p => p.margin && `margin: ${p.margin};`};
    
    &:before {
      content: '';
      display: ${p => p.notIcon ? 'none' : 'blocks'};
      width: 50px;
      height: 43px;
      position: absolute;
      left: -29px;
      top: -32px;
      background: url(${titleImagePurple});
      background-repeat: no-repeat;
      background-size: cover;
      font-weight: 900;
    }
    
    @media (max-width: 600px) {
      font-size: 1.7rem;
    }
`;

export const SectionHeaderWhite = SectionHeader.extend`
  color: white;
  
  &:before {
    background-image: url("${titleImageWhite}");
    left: -31px;
  }
`;


