const ModalCloseButton = styled.button`
  position: absolute;
  top: 20px;
  right: 18px;
  width: 20px;
  height: 20px;
  border: none;
  cursor: pointer;
  background: url(${closeImg}) no-repeat center;
  outline: none;
  transition: all .3s;
  
  &:hover {
    background-image: url(${closePurpleImg});
  }
`;

const SocialsList = styled.ul`
  display: flex;
  list-style: none;
  
  li {
    cursor: pointer;
    transition: all .3s;
    
    &:hover {
      opacity: .7;
    }
  }
`;

export class Leadership extends Component {
  state = {
    showModal: false,
        modalData: advisors[0]
  };

  openModal = (data) => {
    this.setState({showModal: true, modalData: data});
  };

  closeModal = () => {
    this.setState({showModal: false});
  };

  getImgBy = (images) => {
    if (window.innerWidth < 600) {
      return images.img1x;
    }
    if (window.innerWidth < 1700) {
      return images.img2x;
    }

    return images.img3x;
  };

  render() {
    const modalData = this.state.modalData;

    return (
     <Section {...this.props}>
       <Container dir='column' align='center'>
         <Header>Leadership</Header>
         <LeadershipList>
           {leadership1.map(item => (
            <FirstLeadershipItem order={item.order} onClick={() => this.openModal(item)} key={item.name}>
              <img src={item.img} alt=""/>
              <h5>{item.name}</h5>
              <p>{item.presentPost}</p>
            </FirstLeadershipItem>
           ))}
         </LeadershipList>
         <LeadershipList>
           {leadership2.map(item => (
            <SecondLeadershipItem order={item.order} onClick={() => this.openModal(item)} key={item.name}>
              <img src={item.img} alt=""/>
              <h5>{item.name}</h5>
              <p>{item.presentPost}</p>
            </SecondLeadershipItem>
           ))}
         </LeadershipList>
         <Title>Advisors</Title>
         <LeadershipList padding='30px 0'>
           {advisors.map(item => (
            <LeadershipItem onClick={() => this.openModal(item)} key={item.name}>
              <img src={item.img} alt=""/>
              <h5>{item.name}</h5>
              <p>{item.presentPost}</p>
            </LeadershipItem>
           ))}
         </LeadershipList>
       </Container>
       <ReactModal
        isOpen={this.state.showModal}
        onRequestClose={this.closeModal}
        className="modal-content leadership-content"
        overlayClassName="modal-overlay modal-overlay-transparent"
       >
         <ModalPhoto>
           <img src={this.getImgBy(modalData.modal)} alt=""/>
         </ModalPhoto>
         <ModalInfo>
           <h5>{modalData.name}</h5>
           <span>{modalData.presentPost}</span>
           <p>{modalData.modal.desc}</p>
           <SocialsList>
             {modalData.modal.socials.map(item => (
              <li key={item.link}><a href={item.link} target="_blank"><img src={item.img} alt=""/></a></li>
             ))}
           </SocialsList>
         </ModalInfo>
         <ModalCloseButton onClick={this.closeModal}/>
       </ReactModal>
     </Section>
    );
  }
}

