import React, {Component} from 'react';
import styled from 'styled-components';

const LangListContainer = styled.div`
  position: absolute;
  display: ${p => p.open ? 'block' : 'none'};
  min-width: 160px;
  transform: translateY(90%);
  right: 0;
  bottom: 0;
`;

const LangItem = styled.li`
  margin: 15px 0;
  line-height: 20px;
    
  ${p => p.active && `
    font-weight: 600;
    color: #6743DF;
  `}
`;

const LangList = styled.ul`
  background-color: white;
  padding: 1px 24px;
  border-radius: 3px;
  color: #525558;
  font-size: 12px;
  list-style: none;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.09), 0 15px 26px 0 rgba(47,21,136,0.06), 0 0 11px 0 rgba(47,21,136,0.07);
`;

const Arrow = styled.div`
  transform: translateY(1px);
  overflow: hidden;
  height: 11px;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding-right: 17px;

  &:before {
    content: "";
    display: block;
    position: relative;
    border-radius: 2px;
    width: 18px;
    height: 24px;
    background: #fff;
    transform: rotate(45deg);
    top: 5px;
    left: 0;
    box-shadow: 0 2px 9px 0 rgba(62, 57, 255, 0.06);
  }
`;

const LangContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
  cursor: pointer;
  outline: none;
  
  #lang-g, path {
    fill: ${p => p.color};
  }
  
  object {
    cursor: pointer;
  }
`;

const Chevron = styled.div`
   display: flex;
   margin-left: 7px;
`;

const LangIcon = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .3s;
  
  &:hover {
    opacity: .8;
  }
`;

