import React, { Component } from 'react';
import styled from 'styled-components';
import ReactModal from 'react-modal';

import patternHeader from 'assets/images/pattern-header-small.png';
import twitter from 'assets/images/social/twitter.svg';
import facebook from 'assets/images/social/facebook.svg';
import linkedin from 'assets/images/social/linkedin.svg';
import telegram from 'assets/images/social/telegram.svg';
import wechat from 'assets/images/social/wechat.svg';

import closeImg from 'assets/images/close.svg';
import closePurpleImg from 'assets/images/close-purple.svg';

import {Container, Socials, PresaleCard, SectionHeader, Form, ThankForm} from 'ui';

const ModalCloseButton = styled.button`
  position: absolute;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  border: none;
  cursor: pointer;
  background: url(${closeImg}) no-repeat center;
  outline: none;
  transition: all .3s;
  
  &:hover {
    background-image: url(${closePurpleImg});
  }
`;

const Title = styled.h1`
  font-size: 2.625rem;
  font-weight: 900;
`;

const HeroHeader = SectionHeader.extend`
  margin: 64px 0 16px 0;
`;

const SubTitle = styled.h3`
  margin-top: 32px;
  font-size: 1.375rem;
  opacity: .8;
  line-height: 2rem;
  text-align: left;
  font-weight: 600;
`;

const SectionSubHeader = styled.p`
  font-size: 1.125rem;
  margin: 0;
  padding: 0 15px;
  color: #3E466D;
  text-align: center;
`;

const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-bottom: 16px;
  background-color: white;
  margin: 0 auto;
  border-radius: 4px;
  
  @media (max-width: 600px) {
    width: 95%;
    padding: 0 20px;
  }
`;

const HeroWrapper = styled.section`
  padding-top: 190px;
  min-height: 785px;
  color: white;
  text-align: left;
  background-color: #6743DF;
  background-image: url(${patternHeader}), 
                    linear-gradient(45deg, #925DFB 0%, #6743DF 100%);
  background-position: 50% 30px, top left;
  background-repeat: no-repeat;
  position: relative;
  
  @media (max-width: 1000px) {
    background-position: 50% 224%, top left;
  }
  
  @media(max-width: 500px) {
    background-position: 50% 227%, top left;
    padding-top: 110px;
    padding-bottom: 100px;
  }
`;

const Button = styled.button`
  display: flex;
  align-items: center;
  border-radius: 29px;
  padding: 0 92px;
  margin: 32px 0;
  height: 58px;
  border: 1px solid white;
  background-color: #24DE38;
  color: white;
  font-size: 24px;
  font-weight: 800;
  cursor: pointer;
  transition: all .20s ease;
  animation-fill-mode: forwards;
  outline: none;
    
  &:hover {
    transform: translateY(-2px);
  }
`;

const Left = styled.div`
  width: 45%;
`;

const Right = styled.div`
  width: 55%;
  padding-left: 60px;
`;

const Join = styled.p`
  font-size: 20px;
  font-weight: 600;
  line-height: 20px;
  margin: 0;
  margin-bottom: 12px;
  padding-top: 15px;
`;

const MobileContainer = Container.extend`
  flex-direction: column;
  align-items: center;
  
  ${Title} {
    text-align: center;
  }
  
  ${SubTitle} {
    text-align: center;
    margin-bottom: 40px;
  }
  
  @media(max-width: 500px) {
    ${Title} {
      font-size: 30px;
    }
    
    ${SubTitle} {
      font-size: 16px;
      line-height: 24px;
    }
  }
`;

export class Hero extends Component {
  state = {
    mobile: this.isMobile(),
    showModal: false,
    form: {
      success: false,
      pending: false,
      error: false
    }
  };

  componentDidMount() {
    window.addEventListener('resize', this.onResize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onResize);
  }

  onResize = () => {
    if (this.isMobile()) {
      this.setState({mobile: true});
    } else {
      this.setState({mobile: false});
    }
  };

  isMobile() {
    return window.innerWidth <= 1000;
  }

  openModal = () => {
    this.setState({showModal: true});
  };

  closeModal = () => {
    this.setState({showModal: false});
  };

  render() {
    return (
     <HeroWrapper name='top'>
       {this.state.mobile ? <MobileHero onClick={this.openModal}/> : <DesktopContainer  onClick={this.openModal}/>}
       <ReactModal
        isOpen={this.state.showModal}
        onRequestClose={this.closeModal}
        className="modal-content pre-sale"
        overlayClassName="modal-overlay"
       >
         {this.state.form.success ? (
          <ThankForm onClick={this.closeModal}/>
         ) : (
          <FormContainer>
            <ModalCloseButton onClick={this.closeModal}/>
            <HeroHeader textAlign='center' notIcon>Hive Token Pre-sale Form</HeroHeader>
            <SectionSubHeader>Join the revolution in digital identity. Be the owner of your data!</SectionSubHeader>
            <Form margin='36px 0 0' isPending={this.state.form.pending} onSuccess={() => this.setState({form: {
              ...this.state.from,
              success: true,
            }})} width='100%' white/>
          </FormContainer>
         )}
       </ReactModal>
     </HeroWrapper>
    );
  }
}

const socials = [
  {img: twitter, href: 'https://twitter.com/hiveidentity'},
  {img: facebook, href: 'https://www.facebook.com/hiveidentity'},
  {img: linkedin, href: 'https://www.linkedin.com/company/hiveidentity'},
  {img: telegram, href: 'https://t.me/Hive_TGE'},
  {img: wechat, href: 'https://weixin.qq.com/g/AipcMTm3zVuYbS_8'},
];

const MobileHero = ({onClick}) => (
 <MobileContainer>
   <Title>A Decentralized Identity<br/>Management<br/>Ecosystem</Title>
   <SubTitle>Powered by Blockchain Technology<br/>and Tokenization</SubTitle>
   <PresaleCard/>
   <Button onClick={onClick}>Join us</Button>
   <Join>Join the Community</Join>
   <Socials padding='0' opacity='.85' socials={socials}/>
 </MobileContainer>
);

const DesktopContainer = ({onClick}) => (
 <Container align='justify'>
   <Left>
     <Title>A Decentralized Identity<br/>Management<br/>Ecosystem</Title>
     <SubTitle>Powered by Blockchain Technology<br/>and Tokenization</SubTitle>
     <Button onClick={onClick}>Join us</Button>
     <Join>Join the Community</Join>
     <Socials padding='0' opacity='.85' socials={socials}/>
   </Left>
   <Right>
     <PresaleCard/>
   </Right>
 </Container>
);


