import React from 'react';
import styled from 'styled-components';

import {ProgressBar, Timer} from 'ui';

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  border-radius: 6px;
  padding: 36px 40px;
  width: 100%;
  max-width: 600px;
  
  @media(max-width: 550px) {
    padding: 52px 16px;
  }
`;

const CostList = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
`;

const CostItem = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Cost = styled.div`
  font-size: 26px;
  font-weight: bold;
  color: #6743DF;
  //color: rgb(255, 179, 0);
  
  span {
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    color: #3E466D;
    opacity: .5;
  }
  
  @media(max-width: 550px) {
    font-size: 18px;
    
    span {
      font-size: 13px;
    }
  }
`;

const CostDesc = styled.p`
  font-size: 11px;
  color: #3E466D;
  text-transform: uppercase;
  opacity: .7;
  margin: 4px 0px;
  line-height: 17px;
  
  @media(max-width: 550px) {
    font-size: 9px;
  }
`;

const SubTitle = styled.h5`
  color: #3E466D;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  margin: 35px 0 0;
  opacity: .7;
  
  @media(max-width: 550px) {
    font-size: 12px;
  }
`;

const CardFooter = styled.div`
  margin-top: 10px;
  font-weight: bold;
  
  @media (max-width: 650px) {
   font-size: .7rem;
  }
  
  @media (max-width: 370px) {
   font-size: .5rem;
  }
`;

export const PresaleCard = () => (
    <Wrapper>
      <CostList>
        <CostItem>
          <Cost>$5M <span>usd</span></Cost>
          <CostDesc>Soft Cap Reached</CostDesc>
        </CostItem>
        <CostItem>
          <Cost>$18.4M <span>usd</span></Cost>
          <CostDesc>Raised</CostDesc>
        </CostItem>
        <CostItem>
          <Cost>$25M <span>usd</span></Cost>
          <CostDesc>Hard Cap</CostDesc>
        </CostItem>
      </CostList>
      <ProgressBar margin='42px 0 0'>$18.4M</ProgressBar>
      <SubTitle>Pre Sale Ends In</SubTitle>
      <CardFooter>
        <Timer size='3.125em' seconds='70000' to='1535749200000' inverse/>
      </CardFooter>
    </Wrapper>
);


