import React from 'react';
import styled from 'styled-components';

import jumio from 'assets/images/logotypes/jumio.svg';
import goodhire from 'assets/images/logotypes/goodhire.svg';
import linux from 'assets/images/logotypes/linux.svg';
import hyperledger from 'assets/images/logotypes/hyperledger.svg';

import {Container} from 'ui';

const Section = styled.section`
  padding: 135px 0;
  
  @media (max-width: 600px) {
    padding-top: 85px;
  }
`;

const PartnersList = styled.ul`
  display: flex;
  flex-wrap: wrap;
  justify-content: ${p => p.justify ? p.justify : 'space-between'};
  width: 100%;
  list-style: none;
  
  @media (max-width: 970px) {
    justify-content: space-around;
  }
  
  @media (max-width: 450px) {
    flex-direction: column;
    align-items: center;
  }
`;

const PartnersItem = styled.li`
  margin: 20px 35px;
  filter: grayscale(100%);
  opacity: 0.5;
  transition: all .3s;
  
  &:hover {
    filter: grayscale(0%);
    opacity: 1;  
  }
`;

export const StrategicPartners = () => (
    <Section>
      <Container>
        <PartnersList>
          {partners.map(item => <PartnersItem key={item.src}><a href={item.src} target='_blank'><img src={item.img} alt=""/></a></PartnersItem>)}
        </PartnersList>
      </Container>
    </Section>
);

const partners = [
  {img: jumio, src: 'https://www.jumio.com/'},
  {img: goodhire, src: 'https://www.goodhire.com/'},
  {img: linux, src: 'https://www.linuxfoundation.org/'},
  {img: hyperledger, src: 'https://www.hyperledger.org/'},
];


