import React, {Component} from 'react';
import * as d3 from "d3";

import {animate, prepareData, calcStateOnProgress, unionProps} from './chart-helpers';

export class Chart extends Component {
  constructor(props) {
    super();

    this.state = {
      data: prepareData(props.data),
      arcGenerator: d3
       .arc()
       .cornerRadius(props.radius * 0.25)
       .padAngle(0.03)
       .innerRadius(props.radius * 0.84)
       .outerRadius(props.radius),
      animating: false,
      time: 0
    }
  }

  async componentDidMount() {
    this.initializationChart(this.state.data, d3.select(`#CharT g`));

    animate({
      timing: function (t) { return t<.5 ? 16*t*t*t*t*t : 1+16*(--t)*t*t*t*t },
      duration: 1500,
      draw: (progress) => this.drawChartOnProgress(this.state.data, 1 - progress)
    });
  }

  componentWillReceiveProps(nextProps) {
    const mergeData = unionProps(this.props.data, nextProps.data);
    const duration = 1300;
    this.setState({animating: true});

    console.log(mergeData);

    animate({
      timing: function (t) { return t<.5 ? 16*t*t*t*t*t : 1+16*(--t)*t*t*t*t },
      duration,
      draw: (progress) => this.drawChartOnProgress(mergeData, progress),
      callback: () => this.setState({animating: false})
    });
  }

  initializationChart = (data, el) => {
    data.forEach(item => {
      el.append("path")
       .attr("id", item.id)
       .attr("fill", item.fill);
    });

    el.append('image')
     .attr('xlink:href', this.props.img.href)
     .attr('width', this.props.img.width)
     .attr('height', this.props.img.height)
     .attr('x', -this.props.img.width / 2)
     .attr('y', -this.props.img.height / 2)
  };

  drawChart(data) {
    data.forEach(item => {
      d3.select(`#${item.id}`).attr(
       "d",
       this.state.arcGenerator({
         startAngle: item.startAngle,
         endAngle: item.endAngle
       })
      );
    });
  }

  drawChartOnProgress(initialState, progress) {
    this.drawChart(calcStateOnProgress(initialState, progress))
  }

  render() {
    return (
     <svg id="CharT" width={this.props.width} height={this.props.height}>
       <g transform={`translate(${this.props.width / 2}, ${this.props.height / 2})`}/>
     </svg>
    );
  }
}


